import React from 'react';
import { Box } from 'rebass';

function Button(props) {
    return (
    <div>
        <a href={props.link}>
            <Box sx={{height:props.height, width:props.width, backgroundColor:props.color}}>
                {props.text}
            </Box>

        </a>
    </div>)

}

export default Button;